const SEO_RUSH_TOKEN = "3101ad656913045c87a8ea83e1b19698"
const SEO_RUSH_API = "https://api.seorush.io/v1"

interface SEOMetadata {
  title: string
  description: string
  keywords: string[]
  ogImage?: string
  canonical?: string
}

export async function generateSEOMetadata(content: string, url: string): Promise<SEOMetadata> {
  try {
    const response = await fetch(`${SEO_RUSH_API}/generate`, {
      method: "POST",
      headers: {
        "Authorization": `Bearer ${SEO_RUSH_TOKEN}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        content,
        url,
        language: "en",
        optimize: true,
      }),
    })

    if (!response.ok) {
      throw new Error("SEO generation failed")
    }

    const data = await response.json()
    return {
      title: data.title || generateFallbackTitle(content),
      description: data.description || generateFallbackDescription(content),
      keywords: data.keywords || extractKeywords(content),
      ogImage: data.ogImage,
      canonical: url,
    }
  } catch (error) {
    return generateFallbackSEO(content, url)
  }
}

export async function submitToSearchEngines(url: string): Promise<void> {
  try {
    await fetch(`${SEO_RUSH_API}/submit`, {
      method: "POST",
      headers: {
        "Authorization": `Bearer ${SEO_RUSH_TOKEN}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        url,
        engines: ["google", "bing", "yandex"],
      }),
    })
  } catch (error) {
    console.error("Search engine submission failed:", error)
  }
}

export async function generateSitemap(urls: string[]): Promise<string> {
  const sitemap = `<?xml version="1.0" encoding="UTF-8"?>
<urlset xmlns="http://www.sitemaps.org/schemas/sitemap/0.9">
${urls.map(url => `  <url>
    <loc>${url}</loc>
    <lastmod>${new Date().toISOString()}</lastmod>
    <changefreq>daily</changefreq>
    <priority>0.8</priority>
  </url>`).join('\n')}
</urlset>`
  
  return sitemap
}

function generateFallbackTitle(content: string): string {
  const words = content.split(' ').slice(0, 10).join(' ')
  return words.length > 60 ? words.slice(0, 57) + '...' : words
}

function generateFallbackDescription(content: string): string {
  const words = content.split(' ').slice(0, 30).join(' ')
  return words.length > 160 ? words.slice(0, 157) + '...' : words
}

function extractKeywords(content: string): string[] {
  const words = content.toLowerCase().split(/\W+/)
  const stopWords = ['the', 'a', 'an', 'and', 'or', 'but', 'in', 'on', 'at', 'to', 'for']
  const filtered = words.filter(w => w.length > 3 && !stopWords.includes(w))
  const frequency: Record<string, number> = {}
  
  filtered.forEach(word => {
    frequency[word] = (frequency[word] || 0) + 1
  })
  
  return Object.entries(frequency)
    .sort((a, b) => b[1] - a[1])
    .slice(0, 10)
    .map(([word]) => word)
}

function generateFallbackSEO(content: string, url: string): SEOMetadata {
  return {
    title: generateFallbackTitle(content),
    description: generateFallbackDescription(content),
    keywords: extractKeywords(content),
    canonical: url,
  }
}

export async function optimizeForSEO(pageData: {
  title: string
  content: string
  url: string
}): Promise<SEOMetadata> {
  const metadata = await generateSEOMetadata(pageData.content, pageData.url)
  await submitToSearchEngines(pageData.url)
  return metadata
}
