import { NextResponse } from "next/server"
import { generateSitemap } from "@/lib/seo-rush"
import { getSupabaseAdminClient } from "@/lib/supabase/server"

export async function GET() {
  try {
    const supabase = await getSupabaseAdminClient()
    const baseUrl = process.env.NEXTAUTH_URL || "http://localhost:3000"
    
    const urls: string[] = [baseUrl]

    const { data: assets } = await supabase
      .from("assets")
      .select("id")
      .eq("status", "active")
      .limit(1000)

    if (assets) {
      assets.forEach(asset => {
        urls.push(`${baseUrl}/asset/${asset.id}`)
      })
    }

    const { data: threads } = await supabase
      .from("forum_threads")
      .select("id")
      .eq("status", "approved")
      .limit(1000)

    if (threads) {
      threads.forEach(thread => {
        urls.push(`${baseUrl}/forum/thread/${thread.id}`)
      })
    }

    const sitemap = await generateSitemap(urls)

    return new NextResponse(sitemap, {
      headers: {
        "Content-Type": "application/xml",
        "Cache-Control": "public, max-age=3600",
      },
    })
  } catch (error) {
    return NextResponse.json({ error: "Sitemap generation failed" }, { status: 500 })
  }
}
